body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, 'LeagueSpartan', 'GlacialIndifference';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face{
  font-family: "LeagueSpartan";
  src: local("LeagueSpartan"),
  url('./fonts/league-spartan/LeagueSpartan-Bold.otf') format("truetype");
  font-weight: normal;
}

@font-face{
  font-family: "GlacialIndifference";
  src: local("GlacialIndifference"),
  url('./fonts/glacial-indifference.bold.otf') format("truetype");
  font-weight: normal;
}
